<template>
    <div>
        <loader :is-visible="isLoading"></loader>
        <div class="row">
            <div class="col-2">
                <LeftColumn />
            </div>
            <div class="col-10 mt-2 p-0">
                <div class="tab-content" id="myTabContent">
                    <!--LIST TOGGLE FULL SLIDE BUTTON-->
                    <!-- <div class="expand-list">
                        <a href="javascript:;" class="expand-list-btn justify-content-center align-items-center">
                            <em class="icon-solid arrow-solid-left text-white" style="font-size:9pt"></em>
                        </a>
                    </div> -->
                    <!-- start page heading -->
                    <section class="page-action action-margin tablehead">
                        
                            <div class="col-md-12">
                                <div class="theme-primary partition  p-actions-expand">
                                    <span id="titleName" class="p-name text-white">{{ $t('Notification') }}</span>
                                    <span class="p-actions float-right">
                                    </span>
                                    <span class="collapse-head-btns">
                                        <a href="javascript:;" class="collapse-btns"><i
                                                class="fa fa-angle-up text-white"></i></a>
                                    </span>
                                </div>
                            </div>
                       
                    </section>
                </div>
                <div class="container-fluid">
                    <div id="divShowlistingData">
                        <div class="listing float-none">
                            <div class="row">
                                <div class="col-12 py-2">
                                    <div class="mr-2 d-inline-block">
                                    <span class="legends_high colorboxsquare"></span>
                                    {{ $t('SelectNotificationType') }}
                                    </div>
                                </div>
                            </div>
                            <div id="divListing" class="table-responsive">
                                <table id="moduleList" class="table table-bordered dt-responsive nowrap mt-0"
                                    style="width:100%;">
                                    <thead class="thead-bg">
                                        <tr class="gray">
                                            <th colspan="2" style="min-width: 300px;">{{ $t('ManageNotifications') }}</th>
                                            <th class="text-center" style="width:90px" scope="col">
                                                <div class="custom-control custom-checkbox">
                                                    <input type="checkbox" id="chkupper_1" data-change-id="no"
                                                        class="deletable custom-control-input" v-on:click="checkAllRowCheckboxes($event,'chk_1','email')">
                                                    <label class="custom-control-label" for="chkupper_1"></label>
                                                </div>
                                                <span style="min-width:90px" class="font-weight-normal float-left m-1">
                                                {{ $t('Email') }}
                                                </span>
                                            </th>
                                            <th class="text-center" style="width:90px" scope="col">
                                                <div class="custom-control custom-checkbox">
                                                    <input type="checkbox" id="chkupper_2" data-change-id="no"
                                                        class="deletable custom-control-input"  v-on:click="checkAllRowCheckboxes($event,'chk_2','inbox')">
                                                    <label class="custom-control-label" for="chkupper_2"></label>
                                                </div>
                                                <span style="min-width:90px" class="font-weight-normal float-left m-1">
                                                {{ $t('Inbox') }}
                                                </span>
                                            </th>
                                            <th class="text-center" style="width:102px" scope="col">
                                                <div class="custom-control custom-checkbox">
                                                    <input type="checkbox" id="chkupper_3" data-change-id="no"
                                                        class="deletable custom-control-input" v-on:click="checkAllRowCheckboxes($event,'chk_3','mobile')">
                                                    <label class="custom-control-label" for="chkupper_3"></label>
                                                </div>
                                                <span style="min-width:102px" class="font-weight-normal float-left m-1">
                                                {{ $t('Mobile') }}
                                                </span>
                                            </th>
                                            <th class="text-center" style="width:102px" scope="col">
                                                <div class="custom-control custom-checkbox">
                                                    <input type="checkbox" id="chkupper_4" data-change-id="no"
                                                        class="deletable custom-control-input" v-on:click="checkAllRowCheckboxes($event,'chk_4','push')">
                                                    <label class="custom-control-label" for="chkupper_4"></label>
                                                </div>
                                                <span style="min-width:102px" class="font-weight-normal float-left m-1">
                                                {{ $t('Push') }}
                                                </span>
                                            </th>
                                        </tr>
                                    </thead>
                                    <tbody v-for="(parentitem, parentIndex) in GetNotificationData" :key="parentIndex" id="accordionEx">
                                        <tr class="high-bar" :id="'MainColumn' + parentIndex">
                                            <td><span class="table-plus-minus">
                                                    <a class="collapsed" data-parent="#accordionEx"
                                                        :data-target="'#ColumnCollapse' + parentIndex"
                                                        href="javascript:;" aria-expanded="false"
                                                        :aria-controls="'ColumnCollapse' + parentIndex"
                                                        data-toggle="collapse" @click="toggleAccordion(parentIndex)" ><em :class="activeIndex === parentIndex ? 'fa fa-minus pt-0' : 'fa fa-plus pt-0'"
                                                            aria-hidden="true"></em></a></span></td>
                                            <td>
                                                {{ parentitem.ModuleName }}
                                            </td>
                                            <td class="text-center ">
                                                <div class="custom-control custom-checkbox">
                                                    <input type="checkbox" :value="parentitem.ModuleId"
                                                        :id="'chk_1' + parentitem.ModuleId" data-change-id="no"
                                                        v-on:click="checkAllCheckboxes($event,'chk_1' + parentitem.ModuleId,'email',parentIndex)"
                                                        class="deletable custom-control-input" name="email"
                                                        :checked="parentitem.IS_EMAIL_NOTIFICATION == 1 ? 'checked' : ''">
                                                    <label class="custom-control-label"
                                                        :for="'chk_1' + parentitem.ModuleId"></label>
                                                </div>
                                            </td>
                                            <td class="text-center">
                                                <div class="custom-control custom-checkbox">
                                                    <input type="checkbox" :value="parentitem.ModuleId"
                                                        v-on:click="checkAllCheckboxes($event,'chk_2' + parentitem.ModuleId,'inbox',parentIndex)"
                                                        :id="'chk_2' + parentitem.ModuleId" data-change-id="no"
                                                        class="deletable custom-control-input" name="inbox"
                                                        :checked="parentitem.IS_INBOX_NOTIFICATION == 1 ? 'checked' : ''">
                                                    <label class="custom-control-label"
                                                        :for="'chk_2' + parentitem.ModuleId"></label>
                                                </div>
                                            </td>
                                            <td class="text-center">
                                                <div class="custom-control custom-checkbox">
                                                    <input type="checkbox" :value="parentitem.ModuleId"
                                                        v-on:click="checkAllCheckboxes($event,'chk_3' + parentitem.ModuleId,'mobile',parentIndex)"
                                                        :id="'chk_3' + parentitem.ModuleId" data-change-id="no"
                                                        class="deletable custom-control-input" name="mobile"
                                                        :checked="parentitem.IS_MOBILE_NOTIFICATION == 1 ? 'checked' : ''">
                                                    <label class="custom-control-label"
                                                        :for="'chk_3' + parentitem.ModuleId"></label>
                                                </div>
                                            </td>
                                            <td class="text-center">
                                                <div class="custom-control custom-checkbox">
                                                    <input type="checkbox" :value="parentitem.ModuleId"
                                                        v-on:click="checkAllCheckboxes($event,'chk_4' + parentitem.ModuleId,'push',parentIndex)"
                                                        :id="'chk_4' + parentitem.ModuleId" data-change-id="no"
                                                        class="deletable custom-control-input" name="push"
                                                        :checked="parentitem.IS_PUSH_NOTIFICATION == 1 ? 'checked' : ''">
                                                    <label class="custom-control-label"
                                                        :for="'chk_4' + parentitem.ModuleId"></label>
                                                </div>
                                            </td>
                                        </tr>
                                        <tr class="rowdata collapse collapseAppointment gray"
                                            v-for="inneritem in parentitem.GroupedSubmodule"
                                            :id="'ColumnCollapse' + parentIndex" 
                                            v-if="parentitem.GroupedSubmodule.length > 0"
                                            :aria-labelledby="'MainColumn' + parentIndex" data-parent="#accordionEx">
                                            <td></td>
                                            <td>
                                                {{ inneritem.TYPE_NAME }}
                                            </td>
                                            <td class="text-center">
                                                <div class="custom-control custom-checkbox">
                                                    <input type="checkbox" :value="inneritem.ID"
                                                        v-on:click="checkAllOuterMainCheckboxes($event,parentIndex)"
                                                        :id="'chkinner_1' + inneritem.ID" data-change-id="no"
                                                        class="deletable custom-control-input" name="email"
                                                        :checked="inneritem.IS_EMAIL_NOTIFICATION == 1 ? 'checked' : ''">
                                                    <label class="custom-control-label"
                                                        :for="'chkinner_1' + inneritem.ID"></label>
                                                </div>
                                            </td>
                                            <td class="text-center">
                                                <div class="custom-control custom-checkbox">
                                                    <input type="checkbox" :value="inneritem.ID"
                                                        v-on:click="checkAllOuterMainCheckboxes($event,parentIndex)"
                                                        :id="'chkinner_2' + inneritem.ID" data-change-id="no"
                                                        class="deletable custom-control-input" name="inbox"
                                                        :checked="inneritem.IS_INBOX_NOTIFICATION == 1 ? 'checked' : ''">
                                                    <label class="custom-control-label"
                                                        :for="'chkinner_2' + inneritem.ID"></label>
                                                </div>
                                            </td>
                                            <td class="text-center">
                                                <div class="custom-control custom-checkbox">
                                                    <input type="checkbox" :value="inneritem.ID"
                                                        v-on:click="checkAllOuterMainCheckboxes($event,parentIndex)"
                                                        :id="'chkinner_3' + inneritem.ID" data-change-id="no"
                                                        class="deletable custom-control-input" name="mobile"
                                                        :checked="inneritem.IS_MOBILE_NOTIFICATION == 1 ? 'checked' : ''">
                                                    <label class="custom-control-label"
                                                        :for="'chkinner_3' + inneritem.ID"></label>
                                                </div>
                                            </td>
                                             <td class="text-center">
                                                <div class="custom-control custom-checkbox">
                                                    <input type="checkbox" :value="inneritem.ID"
                                                        v-on:click="checkAllOuterMainCheckboxes($event,parentIndex)"
                                                        :id="'chkinner_4' + inneritem.ID" data-change-id="no"
                                                        class="deletable custom-control-input" name="push"
                                                        :checked="inneritem.IS_PUSH_NOTIFICATION == 1 ? 'checked' : ''">
                                                    <label class="custom-control-label"
                                                        :for="'chkinner_4' + inneritem.ID"></label>
                                                </div>
                                            </td>
                                        </tr>
                                    </tbody>
                                </table>
                            </div>
                            <div class="col-lg-12">
                                <div class="float-right mt-3">
                                   <a title="Save" class="btn btn-success mr-2" v-on:click="SubmitNotification"><em class="fa fa-save pr-2"></em>{{$t("Save")}}</a>
                                   <a title="Cancel" class="btn btn-danger" v-on:click="Cancel"><em class="fa fa-close pr-2"></em>{{$t("Cancel")}}</a>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <AddCompanySetup :recordId="recordId" :title="$t('NotificationType')" v-if="showCompanyNotification"
            v-on:close="showCompanyNotification = false; recordId = 0"
            v-on:addReloadData="FetchData(); showCompanyNotification = false; recordId = 0"
            v-bind:moduleName="'CompanyNotification'" v-bind:emitName="'addReloadData'"
            v-bind:addApiUrl="'/CompanyNotification/add'" v-bind:subModule="'CompanyNotification'" />
    </div>
</template>
<script>
import CompanyNotificationDataService from "@/services/DataService";
import AddCompanySetup from "@/components/Modals/AddCompanySetup.vue";
import LeftColumn from "../LeftColumn.vue";
import DataService from "../../../services/DataService";
export default {
    components: {
        LeftColumn,
        AddCompanySetup,
    },
    props: {
        sourcetype:
        {
            type: String,
            required: true
        }
    },
    data() {
        return {
            recordId: 0,
            showCompanyNotification: false,
            noRecord: false,
            isLoading: false,
            GetNotificationData: [],
            GetMainModuleData:[],
            activeIndex: null  // track expanded row
        }
    },
    async created() {
        var vm = this;
        await vm.FetchData();
        setTimeout(function () {
            vm.checkAllCheckboxes();
        }, 1000);
    },
    methods: {
        Cancel(){
            var vm = this;
            vm.isLoading= true;
            vm.FetchData();
            setTimeout(function () {
                vm.checkAllCheckboxes();
                vm.isLoading= false;
        }, 1000);
        },
        toggleAccordion(index) {
        this.activeIndex = this.activeIndex === index ? null : index;
        },

    
  
        async FetchData() {
            
            var vm = this;
            var params = `moduleName=CompanyNotification&submoduleName=CompanyNotification`;
            await CompanyNotificationDataService.GetNotificationTypeForCompany(params).then(response => {
                //debugger;
                if (response.data != null) {
                    var getResponsedata = [];
                    vm.GetNotificationData=[];
                    vm.GetMainModuleData=[];
                    var tempdata = null;
                   
                    getResponsedata = response.data.result;
                    getResponsedata.forEach(item => {
                        if (item.MODULE_ID == tempdata) {
                            var checkifexist = vm.GetNotificationData.findIndex(x => x.ModuleId == tempdata);
                            if (checkifexist > -1) {
                                vm.GetNotificationData[checkifexist].GroupedSubmodule.push(item);
                            }
                        } else {
                            var gettemparray = [];
                            gettemparray.push(item);
                            vm.GetNotificationData.push({
                                ModuleName: item.MODULE_NAME, ModuleId: item.MODULE_ID, IS_EMAIL_NOTIFICATION: item.IS_EMAIL_NOTIFICATION,
                                IS_INBOX_NOTIFICATION: item.IS_INBOX_NOTIFICATION, IS_MOBILE_NOTIFICATION: item.IS_MOBILE_NOTIFICATION,IS_PUSH_NOTIFICATION: item.IS_PUSH_NOTIFICATION
                                , GroupedSubmodule: gettemparray
                            })
                            tempdata = item.MODULE_ID;
                        }
                        vm.GetMainModuleData.push({ id : item.ID,
                            email : item.IS_EMAIL_NOTIFICATION,
                            inbox : item.IS_INBOX_NOTIFICATION,
                            mobile : item.IS_MOBILE_NOTIFICATION,
                            push : item.IS_PUSH_NOTIFICATION});
                    })
                }
            })
        },
        CheckMainCheckboxes(id, value) {
            var vm = this;
            var array = document.getElementsByTagName("input");
            for (var i = 0; i < array.length; i++) {
                if (array[i].type == "checkbox") {
                    if (array[i].id.includes(id)) {
                        array[i].checked = value;
                    }
                }
            }
        },
        checkAllinnerCheckboxes(value,array){
            var vm = this;
            vm.GetNotificationData.forEach(item=>{
                if(item.ModuleId === parseInt(array.value)){
                    item.GroupedSubmodule.forEach(inneritem=>{
                        if(value == 'email'){
                            inneritem.IS_EMAIL_NOTIFICATION = array.checked == true ? 1 : 0;
                            item.IS_EMAIL_NOTIFICATION = array.checked == true ? 1 : 0;
                        }else if(value == 'inbox'){
                            inneritem.IS_INBOX_NOTIFICATION = array.checked == true ? 1 : 0;
                            item.IS_INBOX_NOTIFICATION = array.checked == true ? 1 : 0;
                        }else if(value == 'mobile'){
                            inneritem.IS_MOBILE_NOTIFICATION = array.checked == true ? 1 : 0;
                            item.IS_MOBILE_NOTIFICATION = array.checked == true ? 1 : 0;
                        }
                        else if(value == 'push'){
                            inneritem.IS_PUSH_NOTIFICATION = array.checked == true ? 1 : 0;
                            item.IS_PUSH_NOTIFICATION = array.checked == true ? 1 : 0;
                        }
                        var checkexist = vm.GetMainModuleData.find(x=>x.id === inneritem.ID);
                        if(checkexist != undefined){
                            checkexist.id = inneritem.ID;
                            checkexist.email = inneritem.IS_EMAIL_NOTIFICATION;
                            checkexist.inbox = inneritem.IS_INBOX_NOTIFICATION;
                            checkexist.mobile = inneritem.IS_MOBILE_NOTIFICATION;
                            checkexist.push = inneritem.IS_PUSH_NOTIFICATION;
                        }else{
                            vm.GetMainModuleData.push({id:inneritem.ID,email:inneritem.IS_EMAIL_NOTIFICATION,inbox:inneritem.IS_INBOX_NOTIFICATION,mobile:inneritem.IS_MOBILE_NOTIFICATION,push:inneritem.IS_PUSH_NOTIFICATION})
                        }
                    })
                }
            })
        },
        checkAllCheckboxes(event,chkvalue,value,index) {
            var vm = this;
            var inneritemvalue = true;
            var mainoutervalue = true;
            if(event != undefined){
                vm.GetNotificationData.forEach((item,outerindex)=>{
                    item.GroupedSubmodule.forEach((inneritem,innerindex)=>{
                        if(event.target.value == inneritem.MODULE_ID && index == outerindex && value == 'email'){
                            vm.checkAllinnerCheckboxes(value,event.target);
                        }else if(event.target.value == inneritem.MODULE_ID && index == outerindex && value == 'inbox'){
                            vm.checkAllinnerCheckboxes(value,event.target);
                        }else if(event.target.value == inneritem.MODULE_ID && index == outerindex && value == 'mobile'){
                            vm.checkAllinnerCheckboxes(value,event.target);
                        }
                        else if(event.target.value == inneritem.MODULE_ID && index == outerindex && value == 'push'){
                            vm.checkAllinnerCheckboxes(value,event.target);
                        }
                    })
                    if(item.IS_EMAIL_NOTIFICATION == 0 && value == 'email'){
                        mainoutervalue = false;
                    }else if(item.IS_INBOX_NOTIFICATION == 0 && value == 'inbox'){
                        mainoutervalue = false;
                    }else if(item.IS_MOBILE_NOTIFICATION == 0 && value == 'mobile'){
                        mainoutervalue = false;
                    }
                    else if(item.IS_PUSH_NOTIFICATION == 0 && value == 'push'){
                        mainoutervalue = false;
                    }
                })
                if(value == 'email'){
                    mainoutervalue == false ? vm.CheckMainCheckboxes('chkupper_1', '') : vm.CheckMainCheckboxes('chkupper_1', 'checked');
                }else if (value == 'inbox'){
                    mainoutervalue == false? vm.CheckMainCheckboxes('chkupper_2', '') : vm.CheckMainCheckboxes('chkupper_2', 'checked');
                }else if (value == 'mobile'){
                    mainoutervalue == false ? vm.CheckMainCheckboxes('chkupper_3', '') : vm.CheckMainCheckboxes('chkupper_3', 'checked');
                }
                else if (value == 'push'){
                    mainoutervalue == false ? vm.CheckMainCheckboxes('chkupper_4', '') : vm.CheckMainCheckboxes('chkupper_4', 'checked');
                }
            }else{
                var array = document.getElementsByClassName("deletable custom-control-input");
                for (var i = 0; i < array.length; i++) {
                    if (array[i].type == "checkbox") {
                        if ((array[i].id.includes('chk_1') || array[i].id.includes('chkinner_1')) && chkvalue != undefined ? chkvalue == array[i].id : array[i].id.includes('chk_1') || array[i].id.includes('chkinner_1')) {
                            if (array[i].checked == true) {
                                vm.CheckMainCheckboxes('chkupper_1', 'checked');
                            } else {
                                vm.CheckMainCheckboxes('chkupper_1', '');
                                break;
                            }
                        } else if ((array[i].id.includes('chk_2') || array[i].id.includes('chkinner_2')) && chkvalue != undefined ? chkvalue == array[i].id : array[i].id.includes('chk_2') || array[i].id.includes('chkinner_2')) {
                            if (array[i].checked == true) {
                                vm.CheckMainCheckboxes('chkupper_2', 'checked');
                            } else {
                                vm.CheckMainCheckboxes('chkupper_2', '');
                                break;
                            }
                        } else if ((array[i].id.includes('chk_3') || array[i].id.includes('chkinner_3')) && chkvalue != undefined ? chkvalue == array[i].id : array[i].id.includes('chk_3') || array[i].id.includes('chkinner_3')) {
                            if (array[i].checked == true) {
                                vm.CheckMainCheckboxes('chkupper_3', 'checked');
                            } else {
                                vm.CheckMainCheckboxes('chkupper_3', '');
                                break;
                            }
                        }
                        else if ((array[i].id.includes('chk_4') || array[i].id.includes('chkinner_4')) && chkvalue != undefined ? chkvalue == array[i].id : array[i].id.includes('chk_4') || array[i].id.includes('chkinner_4')) {
                            if (array[i].checked == true) {
                                vm.CheckMainCheckboxes('chkupper_4', 'checked');
                            } else {
                                vm.CheckMainCheckboxes('chkupper_4', '');
                                break;
                            }
                        }
                    }
                }
            }
        },
        async SubmitNotification(){
            var vm = this;
            var obj={
                MainData:vm.GetMainModuleData
            };
            //debugger;
            await DataService.UpdateCompanyNotification(obj).then(response=>{      
                 if (response.data.Result != null && response.data.Result.MAIN_ID > 0) {
                    this.ShowAlert(this.$t('UpdatedSuccess',true,[this.$t('Notifications')]), 'success', true, this.$t('Alert'));
                        this.$emit(this.emitName)
                    } else {
                        if(this.$t(response.data.Status) == 'Failure'){
                            this.ShowAlert(this.$t('ErrorWhileSavingRecords'), "danger", false, this.$t("Alert"));
                        }
                     }
            }).catch(err=>{
                console.log(err);
            })
        },
        checkAllRowCheckboxes(event,value,type) {
            var vm = this;
            var array = document.getElementsByClassName("deletable custom-control-input");
            for (var i = 0; i < array.length; i++) {
                if (array[i].type == "checkbox" && array[i].id.includes(value)) {
                    vm.GetNotificationData.forEach(item=>{
                        item.GroupedSubmodule.forEach(inneritem=>{
                            if(type == 'email'){
                                inneritem.IS_EMAIL_NOTIFICATION = event.target.checked == true ? 1 : 0;
                                item.IS_EMAIL_NOTIFICATION = event.target.checked == true ? 1 : 0;
                            }
                            else if (type == 'inbox'){
                                inneritem.IS_INBOX_NOTIFICATION = event.target.checked == true ? 1 : 0;
                                item.IS_INBOX_NOTIFICATION = event.target.checked == true ? 1 : 0;
                            }
                            else if (type == 'mobile'){
                                inneritem.IS_MOBILE_NOTIFICATION = event.target.checked == true ? 1 : 0;
                                item.IS_MOBILE_NOTIFICATION = event.target.checked == true ? 1 : 0;
                            }
                            else if (type == 'push'){
                                inneritem.IS_PUSH_NOTIFICATION = event.target.checked == true ? 1 : 0;
                                item.IS_PUSH_NOTIFICATION = event.target.checked == true ? 1 : 0;
                            }
                            var checkexist = vm.GetMainModuleData.find(x=>x.id === inneritem.ID);
                            if(checkexist != undefined){
                                checkexist.id = inneritem.ID;
                                checkexist.email = inneritem.IS_EMAIL_NOTIFICATION;
                                checkexist.inbox = inneritem.IS_INBOX_NOTIFICATION;
                                checkexist.mobile = inneritem.IS_MOBILE_NOTIFICATION;
                                checkexist.push = inneritem.IS_PUSH_NOTIFICATION;
                            }else{
                                vm.GetMainModuleData.push({id:inneritem.ID,email:inneritem.IS_EMAIL_NOTIFICATION,inbox:inneritem.IS_INBOX_NOTIFICATION,mobile:inneritem.IS_MOBILE_NOTIFICATION,push:inneritem.IS_PUSH_NOTIFICATION})
                            }
                        })
                    })
                }   
            }
        },
        checkAllOuterMainCheckboxes(event,index){
            var vm = this;
            var isCheckedAll = true;
            var isCheckedMainAll = true;
            vm.GetNotificationData.forEach((item,outerindex)=>{
                item.GroupedSubmodule.forEach((inneritem,innerindex)=>{
                    if(event.target.name == 'email' && inneritem.ID == event.target.value){
                        inneritem.IS_EMAIL_NOTIFICATION = event.target.checked == true ? 1 : 0;
                    }else if (event.target.name == 'inbox' && inneritem.ID == event.target.value){
                        inneritem.IS_INBOX_NOTIFICATION = event.target.checked == true ? 1 : 0;
                    }else if (event.target.name == 'mobile' && inneritem.ID == event.target.value){
                        inneritem.IS_MOBILE_NOTIFICATION = event.target.checked == true ? 1 : 0;
                    }
                    else if (event.target.name == 'push' && inneritem.ID == event.target.value){
                        inneritem.IS_PUSH_NOTIFICATION = event.target.checked == true ? 1 : 0;
                    }
                    if(index == outerindex && inneritem.ID == event.target.value){
                        var checkexist = vm.GetMainModuleData.find(x=>x.id === inneritem.ID);
                        if(checkexist != undefined){
                            checkexist.id = inneritem.ID;
                            checkexist.email = inneritem.IS_EMAIL_NOTIFICATION;
                            checkexist.inbox = inneritem.IS_INBOX_NOTIFICATION;
                            checkexist.mobile = inneritem.IS_MOBILE_NOTIFICATION;
                            checkexist.push = inneritem.IS_PUSH_NOTIFICATION;
                        }else{
                            vm.GetMainModuleData.push({id:inneritem.ID,email:inneritem.IS_EMAIL_NOTIFICATION,inbox:inneritem.IS_INBOX_NOTIFICATION,mobile:inneritem.IS_MOBILE_NOTIFICATION,push:inneritem.IS_PUSH_NOTIFICATION})
                        }
                    }

                    if(inneritem.IS_EMAIL_NOTIFICATION == 0 && index == outerindex && event.target.name == 'email'){
                        item.IS_EMAIL_NOTIFICATION = 0;
                        isCheckedAll = false;
                    }else if(inneritem.IS_INBOX_NOTIFICATION == 0 && index == outerindex && event.target.name == 'inbox'){
                        item.IS_INBOX_NOTIFICATION = 0;
                        isCheckedAll = false;
                    }else if(inneritem.IS_MOBILE_NOTIFICATION == 0 && index == outerindex && event.target.name == 'mobile'){
                        item.IS_MOBILE_NOTIFICATION = 0;
                        isCheckedAll = false;
                    }
                    else if(inneritem.IS_PUSH_NOTIFICATION == 0 && index == outerindex && event.target.name == 'push'){
                        item.IS_PUSH_NOTIFICATION = 0;
                        isCheckedAll = false;
                    }
                })
                if(isCheckedAll && index == outerindex && event.target.name == 'email'){
                    item.IS_EMAIL_NOTIFICATION = 1;
                }else if(isCheckedAll && index == outerindex && event.target.name == 'inbox'){
                    item.IS_INBOX_NOTIFICATION = 1;
                }else if(isCheckedAll && index == outerindex && event.target.name == 'mobile'){
                    item.IS_MOBILE_NOTIFICATION = 1;
                }
                else if(isCheckedAll && index == outerindex && event.target.name == 'push'){
                    item.IS_PUSH_NOTIFICATION = 1;
                }

                if(item.IS_EMAIL_NOTIFICATION == 0 && event.target.name == 'email'){isCheckedMainAll = false}
                else if(item.IS_INBOX_NOTIFICATION == 0 && event.target.name == 'inbox'){isCheckedMainAll = false}
                else if(item.IS_MOBILE_NOTIFICATION == 0 && event.target.name == 'mobile'){isCheckedMainAll = false}
                else if(item.IS_PUSH_NOTIFICATION == 0 && event.target.name == 'push'){isCheckedMainAll = false}
                if (event.target.name == 'email'){if(!isCheckedMainAll){vm.CheckMainCheckboxes('chkupper_1','')}else{vm.CheckMainCheckboxes('chkupper_1','checked')}}
                else if(event.target.name == 'inbox'){if(!isCheckedMainAll){vm.CheckMainCheckboxes('chkupper_2','')}else{vm.CheckMainCheckboxes('chkupper_2','checked')}}
                else if(event.target.name == 'mobile'){if(!isCheckedMainAll){vm.CheckMainCheckboxes('chkupper_3','')}else{vm.CheckMainCheckboxes('chkupper_3','checked')}}
                else if(event.target.name == 'push'){if(!isCheckedMainAll){vm.CheckMainCheckboxes('chkupper_4','')}else{vm.CheckMainCheckboxes('chkupper_4','checked')}}
            })
        }
    }
}
</script>